        </div>
      </div>
    </div>-->
    <div class="seachDataArea" v-loading.lock="loadingFlag">
      <div class="dataTitle">
        <div class="dataIconArear">
          <i class="el-icon-document">数据列表</i>
        </div>
        <!-- <div class="addButtom" @click="showAddDialog">添加</div> -->
      </div>
      <div class="dataLsitArea">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="staff_id" label="员工编号"></el-table-column>
          <el-table-column label="头像" width="100">
            <template slot-scope="scope">
              <div
                class="listAvatar"
                :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + scope.row.avatar + '.jpg)'}"
              ></div>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column prop="department" label="部门"></el-table-column>
          <el-table-column prop="postion" label="职务"></el-table-column>
          <el-table-column prop="start" label="合同开始时间"></el-table-column>
          <el-table-column prop="end" label="合同结束时间"></el-table-column>
          <el-table-column prop="conremark" label="备注"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope" style="oveflow:hidden">
              <div class="buttomDetail" @click="handleDetail(scope.$index, scope.row)">详情</div>
              <!-- <div class="buttomDelete" @click="handleDelete(scope.$index, scope.row)">删除</div> -->
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="bottomArea">
        <el-pagination
          background
          layout="prev, pager, next, total, jumper"
          :total="tableData.length"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>

    <el-dialog title="合同详情" :visible.sync="dialogTableVisible">
      <table width="100%" class="infoTable">
        <tr>
          <td class="personAvatar" rowspan="2">
            <div
              class="listAvatar"
              :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + item.avatar + '.jpg)'}"
            ></div>
          </td>
          <td>员工编号：{{ item.staff_id }}</td>
          <td>姓名： {{ item.name }}</td>
          <td>性别：{{ item.sex }}</td>
          <td>
            职务：
            <el-input v-model="item.postion" placeholder="职务" :disabled="!identity"></el-input>
          </td>
        </tr>
        <tr>
          <td>
            合同开始时间：
            <el-input v-model="item.start" placeholder="合同开始时间" :disabled="!identity"></el-input>
          </td>
          <td>
            合同结束时间：
            <el-input v-model="item.end" placeholder="合同结束时间" :disabled="!identity"></el-input>
          </td>
          <td>
            合同内容：
            <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              v-model="item.content"
              :disabled="!identity"
            ></el-input>
          </td>
        </tr>
      </table>
      <div style="overflow: hidden;">
        <div class="buttomOk" @click="saveContract(item)" v-show="identity">保存</div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "contractManage",
  components: {},
  data() {
    return {
      input: "",
      currpage: 1,
      dialogTableVisible: false,
      item: "",
      tableData: [],
      loadingFlag: true,
      addContractFlag: false,
      attendan: {
        people_id: "",
        department_id: "",
        time: "",
        position: "",
        end: "",
        contarct_content: "",
        profile_name: "",
        profile_abstract: "",
        profiel_remark: ""
      },
      identity: "",
      options: []
    };
  },
  methods: {
    handleDelete(index, row) {
      console.log(index, row);
    },
    handleDetail(index, row) {
      this.dialogTableVisible = true;
      this.item = row;
    },
    // 分页切换
    handleCurrentChange(cpage) {
      this.currpage = cpage;
    },
    getAllContract() {
      this.$axios({
        method: "post",
        url: "/contract/list",
        data: { staff_id: "" },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        if (resData.code == 0) {
          this.tableData = resData.data;
          this.loadingFlag = false;
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
        }
      });
    },
    // 保存
    saveContract(item) {
      this.$axios({
        method: "post",
        url: "/staff/contract",
        data: {
          staff_id: item.staff_id,
          position: item.postion,
          begin: item.start,
          end: item.end,
          content: item.content
        },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        if (resData.code == 0) {
          this.getAllContract();
          this.$message({
            message: "修改成功",
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
          if (resData.msg == "登录信息失效，请重新登录") {
            this.$router.push({ path: "/" });
          }
        }
      });
    },
    showAddDialog() {
      this.addContractFlag = true;
    }
  },
  mounted() {
    this.token = this.$store.getters.getUserToken;
    var identity = this.$store.getters.getIdentity;
    if (identity) {
      this.identity = true;
    } else {
      this.identity = false;
    }
    this.getAllContract();
  }
};
</script>

<style scoped>
.contractManage {
  width: 100%;
  height: 100%;
}
.seachArea1 {
  width: 100%;
  height: 20vh;
  box-sizing: border-box;
