    </div>-->
    <div class="seachDataArea" v-loading.lock="loadingFlag">
      <div class="dataTitle">
        <div class="dataIconArear">
          <i class="el-icon-document">数据列表</i>
        </div>
      </div>
      <div class="dataLsitArea">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="id" label="人员Id"></el-table-column>
          <el-table-column label="头像" width="100">
            <template slot-scope="scope">
              <div
                class="listAvatar"
                :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + scope.row.people_id + '.jpg)'}"
              ></div>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column prop="sex" label="性别"></el-table-column>
          <el-table-column prop="department" label="部门"></el-table-column>
          <el-table-column prop="position" label="职务"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope" style="oveflow:hidden">
              <div class="buttomDetail" @click="handleDetail(scope.$index, scope.row)">详情</div>
              <div
                class="buttomDelete"
                @click="handleDelete(scope.$index, scope.row)"
                v-show="identity"
              >删除</div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="bottomArea">
        <el-pagination
          background
          layout="prev, pager, next, total, jumper"
          :total="5"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>
    <el-dialog :title="personalInfo.name + '个人信息'" :visible.sync="dialogTableVisible" width="80%">
      <table width="100%" class="infoTable" v-loading.lock="detailLoading">
        <tr>
          <td class="personAvatar" rowspan="2">
            <div
              class="listAvatar"
              :style="{backgroundImage:'url(http://jiaowu.sicau.edu.cn/photo/' + personalInfo.people_id + '.jpg)'}"
            ></div>
          </td>
          <td>员工编号：{{ personalInfo.id }}</td>
          <td style="width:10vw;">
            姓名
            <el-input v-model="personalInfo.name" placeholder="姓名" :disabled="!identity"></el-input>
          </td>
          <td>性别：{{ personalInfo.sex }}</td>
          <td>部门：{{ personalInfo.department }}</td>
          <td>职务：{{ personalInfo.position }}</td>
        </tr>
        <tr>
          <td>出生日期：{{ personalInfoHide.birth }}</td>
          <td colspan="2">
            电话号码：
            <el-input v-model="personalInfo.tel" placeholder="电话号码" :disabled="!identity"></el-input>
          </td>
          <td>学历：{{ personalInfoHide.degree }}</td>
          <td colspan="2">入职时间：{{ personalInfoHide.register_date }}</td>
        </tr>
        <tr>
          <td colspan="2">
            邮箱地址：
            <el-input v-model="personalInfo.email" placeholder="邮箱地址" :disabled="!identity"></el-input>
          </td>
          <td colspan="5">
            备注：
            <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              v-model="personalInfo.remark"
              :disabled="!identity"
            ></el-input>
          </td>
        </tr>
      </table>
      <div style="overflow: hidden;">
        <div class="buttomOk" @click="saveContract(personalInfo)" v-show="identity">保存</div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { constants } from "crypto";
export default {
  name: "fileManage",
  components: {},
  data() {
    return {
      input: "",
      currpage: 1,
      dialogTableVisible: false,
      tableData: [],
      personalInfo: {},
      personalInfoHide: {},
      token: "",
      loadingFlag: true,
      detailLoading: true,
      identity: ""
    };
  },
  methods: {
    handleDelete(index, row) {
      console.log(index, row);
      var item = row;
      // 删除
      this.$axios({
        method: "post",
        url: "/staff/del",
        data: {
          id: item.id
        },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        console.log(resData);
        if (resData.code == 0) {
          this.getAllUserMsg();
          this.$message({
            message: "修改成功",
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
          if (resData.msg == "登录信息失效，请重新登录") {
            this.$router.push({ path: "/" });
          }
        }
      });
    },
    handleDetail(index, row) {
      var people_id = row.people_id;
      this.personalInfo = row;
      this.dialogTableVisible = true;
      this.$axios({
        method: "post",
        url: "people/list",
        data: { people_id: people_id },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        if (resData.code == 0) {
          this.personalInfoHide = resData.data[0];
          this.detailLoading = false;
        } else {
          this.$message({
            showClose: true,
            message: "服务器错误，请稍后重试",
            type: "error"
          });
        }
      });
    },
    // 分页切换
    handleCurrentChange(cpage) {
      this.currpage = cpage;
    },
    getAllUserMsg() {
      this.$axios({
        method: "post",
        url: "/staff/list",
        data: { staff_id: "" },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        console.log(response);
        var resData = response.data;
        if (resData.code == 0) {
          this.tableData = resData.data;
          this.loadingFlag = false;
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
          if (resData.msg == "登录信息失效，请重新登录") {
            this.$router.push({ path: "/" });
          }
        }
      });
    },
    // 修改
    saveContract(item) {
      this.$axios({
        method: "post",
        url: "/staff/update",
        data: {
          id: item.id,
          name: item.name,
          tel: item.tel,
          email: item.email,
          remark: item.remark
        },
        headers: {
          "Content-Type": "application/json",
          token: this.token
        }
      }).then(response => {
        var resData = response.data;
        if (resData.code == 0) {
          this.getAllUserMsg();
          this.$message({
            message: "修改成功",
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: resData.msg,
            type: "error"
          });
          if (resData.msg == "登录信息失效，请重新登录") {
            this.$router.push({ path: "/" });
          }
        }
      });
    }
  },
  mounted() {
    this.token = this.$store.getters.getUserToken;
    var identity = this.$store.getters.getIdentity;
    if (identity) {
      this.identity = true;
    } else {
      this.identity = false;
    }
    this.getAllUserMsg();
  }
};
</script>

<style>
.fileManage {
  width: 100%;
  height: 100%;
}
.seachArea {
  width: 100%;
  height: 25vh;
  box-sizing: border-box;
  border: 1px solid #dcdfe6;
  position: relative;
}
.iconArear {
  height: 6vh;
  position: absolute;
  top: 0;
  left: 0;
}
.iconArear > i {
  font-size: 1vw;
